<template>
  <div class="rightContent">
    <div class="topArea">
      <div class="avatarBox">
        <el-avatar :size="40" :src="circleUrl"></el-avatar>
      </div>
    </div>
    <div class="contentBox">
      <router-view></router-view>
      <!-- <div class="filterBox">
        
      </div>-->
    </div>
  </div>
</template>

<script>
import Index from "@/components/right/Index";
import Compare from "@/components/right/Compare";
import Analysis from "@/components/right/Analysis";

export default {
  name: "RightContent",
  data() {
    return {
      circleUrl:
        "https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"
    };
  },
  methods: {},
  components: {
    Index,
    Compare,
    Analysis
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.rightContent {
  height: 100%;
}
.topArea {
  height: 40px;
  border-bottom: 1px solid #ddd;
  padding: 10px 15px;
  /* background:seagreen; */
}
.avatarBox {
  float: right;
}
.contentBox {
  position: absolute;
  top: 61px;
  bottom: 0;
  left: 201px;
  right: 0;
  overflow-y: scroll;
  background: #f5f6f7;
}

</style>



