import Vue from 'vue'
import Router from 'vue-router'
import LoginPage from '@/components/login/LoginPage'
import Home from '@/components/Home'
import Compare from '@/components/right/Compare'
import Analysis from '@/components/right/Analysis'
import Index from '@/components/right/Index'
Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/', //<----【看这里】
            component: LoginPage,
            meta: {
                title: '乐悦平台登录页'
            }
        },
        {
            path: '/login', //<----【看这里】
            component: LoginPage,
            meta: {
                title: '乐悦平台登录页'
            }
        },
        {
            path: '/home',//<----【看这里】
            component: Home,
            meta: {
                title: '乐悦平台'
            },
            redirect:'/index',
            children: [
                {
                    path: '/index',//<----【看这里】
                    component: Index,
                    meta: {
                        title: '乐悦平台'
                    },
                },
                {
                    path: '/compare',//<----【看这里】
                    component: Compare
                },
                {
                    path: '/analysis',//<----【看这里】
                    component: Analysis
                }
            ]
        }
    ]
})


