<template>
  <div class="compare">
    <p>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>知航览市</el-breadcrumb-item>
        <el-breadcrumb-item>中转分析</el-breadcrumb-item>
        <el-breadcrumb-item>机场中转对比</el-breadcrumb-item>
      </el-breadcrumb>
    </p>
    <div class="filterBox clearfix">
      <div class="filterItem">
        <label>机场选择</label>
        <el-select v-model="defaultValue" size="small" placeholder="请选择">
          <el-option
            v-for="item in airports"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>
      <div class="filterItem">
        <label>日期选择</label>
        <el-date-picker
          v-model="dateValue"
          type="date"
          size="small"
          placeholder="选择日期"
          value-format="yyyy-MM-dd"
        ></el-date-picker>
      </div>
      <div class="filterItem">
        <el-button type="primary" size="small" @click="queryData">查询</el-button>
      </div>
    </div>
    <div class="queryBody">
      <el-table v-loading="loading" :data="tableData" style="width: 100%" size="medium ">
        <el-table-column prop="date" label="日期" width="180"></el-table-column>
        <el-table-column prop="name" label="姓名" width="180"></el-table-column>
        <el-table-column prop="address" label="地址"></el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Qs from "qs";

export default {
  name: "Compare",
  data() {
    return {
      airports: [],//机场下拉列表数据
      defaultValue: "",
      dateValue: "",
      tableData: [],
      loading: false
    };
  },
  methods: {
    queryData: function() {
      //打开加载特效
      this.loading = true;
      //获取页面查询数据
      let data = Qs.stringify({
        airport: this.defaultValue,
        date: this.dateValue
      });
      this.$axios
        .post(
          "https://4d50f24f-c789-43eb-8a8b-a9973eb05473.mock.pstmn.io/skyinsight/compare",
          data
        )
        .then(res => {
          this.loading = false;//关闭加载特效
          this.tableData = res.data;
          console.log("res=>", res);
        });
    }
  },
  mounted() {
    //获取机场下拉列表数据
    this.$axios
      .get(
        "https://4d50f24f-c789-43eb-8a8b-a9973eb05473.mock.pstmn.io/skyinsight/airports"
      )
      .then(res => {
        this.airports = res.data;
      })
      .catch(res => {
        console.log(res);
      });
    //初始化日期选择器
    this.dateValue = new Date().format("yyyy-MM-dd");
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-button--small {
  font-size: 14px;
}
.clearfix:after {
  content: "";
  display: block;
  clear: both;
}
.compare {
  margin: 20px;
}
.filterBox,
.queryBody {
  padding: 20px;
  background: #fff;
  border-radius: 3px;
}
.queryBody {
  margin-top: 20px;
}
.filterItem {
  float: left;
  margin-right: 25px;
}
label {
  color: #303113;
  margin-right: 8px;
}
</style>



