<template>
  <div class="analysis">
    <p>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>知航览市</el-breadcrumb-item>
        <el-breadcrumb-item>中转分析</el-breadcrumb-item>
        <el-breadcrumb-item>中转机会分析</el-breadcrumb-item>
      </el-breadcrumb>
    </p>
    <div class="filterBox clearfix">
      <div class="filterItem">
        <label>机场选择</label>
        <el-select v-model="defaultValue" size="small" placeholder="请选择">
          <el-option
            v-for="item in airports"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>
      <div class="filterItem">
        <label>日期选择</label>
        <el-date-picker v-model="dateValue" type="date" size="small" placeholder="选择日期"></el-date-picker>
      </div>
      <div class="filterItem">
        <el-button type="primary" size="small">查询</el-button>
      </div>
    </div>
    <div class="queryBody">
      <div id="chartLine"></div>
      <div id="chartBar"></div>
    </div>
  </div>
</template>

<script>
import echarts from "echarts";
export default {
  name: "Analysis",
  data() {
    return {
      airports: [
        {
          value: "1",
          label: "双流机场"
        },
        {
          value: "2",
          label: "白云机场"
        },
        {
          value: "3",
          label: "南苑机场"
        }
      ],
      defaultValue: "",
      dateValue: ""
    };
  },
  mounted() {
    this.chartLine = echarts.init(document.getElementById("chartLine"));

    // 指定图表的配置项和数据
    var option = {
      tooltip: {
        //设置tip提示
        trigger: "axis"
      },

      legend: {
        //设置区分（哪条线属于什么）
        data: ["平均成绩", "学生成绩"]
      },
      color: ["#8AE09F", "#FA6F53"], //设置区分（每条线是什么颜色，和 legend 一一对应）
      xAxis: {
        //设置x轴
        type: "category",
        boundaryGap: false, //坐标轴两边不留白
        data: [
          "2019-1-1",
          "2019-2-1",
          "2019-3-1",
          "2019-4-1",
          "2019-5-1",
          "2019-6-1",
          "2019-7-1"
        ],
        name: "DATE", //X轴 name
        nameTextStyle: {
          //坐标轴名称的文字样式
          color: "#FA6F53",
          fontSize: 16,
          padding: [0, 0, 0, 20]
        },
        axisLine: {
          //坐标轴轴线相关设置。
          lineStyle: {
            color: "#FA6F53"
          }
        }
      },
      yAxis: {
        name: "SALES VOLUME",
        nameTextStyle: {
          color: "#FA6F53",
          fontSize: 16,
          padding: [0, 0, 10, 0]
        },
        axisLine: {
          lineStyle: {
            color: "#FA6F53"
          }
        },
        type: "value"
      },
      series: [
        {
          name: "平均成绩",
          data: [220, 232, 201, 234, 290, 230, 220],
          type: "line", // 类型为折线图
          lineStyle: {
            // 线条样式 => 必须使用normal属性
            normal: {
              color: "#8AE09F"
            }
          }
        },
        {
          name: "学生成绩",
          data: [120, 200, 150, 80, 70, 110, 130],
          type: "line",
          lineStyle: {
            normal: {
              color: "#FA6F53"
            }
          }
        }
      ]
    };

    // 使用刚指定的配置项和数据显示图表。
    this.chartLine.setOption(option);

    this.chartBar = echarts.init(document.getElementById("chartBar"));

    // 指定图表的配置项和数据
    var option1 = {
      xAxis: {
        type: "category",
        data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"]
      },
      yAxis: {
        type: "value"
      },
      series: [
        {
          data: [120, 200, 150, 80, 70, 110, 130],
          type: "bar",
          showBackground: true,
          backgroundStyle: {
            color: "rgba(220, 220, 220, 0.8)"
          }
        }
      ]
    };

    // 使用刚指定的配置项和数据显示图表。
    this.chartBar.setOption(option1);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-button--small {
  font-size: 14px;
}
.clearfix:after {
  content: "";
  display: block;
  clear: both;
}
.analysis {
  margin: 20px;
}
.filterBox,
.queryBody {
  padding: 20px;
  background: #fff;
  border-radius: 3px;
}
.queryBody {
  margin-top: 20px;
}
.filterItem {
  float: left;
  margin-right: 25px;
}
label {
  color: #303113;
  margin-right: 8px;
}
#chartLine {
  width: 100%;
  height: 300px;
}
#chartBar {
  width: 100%;
  height: 300px;
}
</style>



