<template>
  <div class="leftMenu">
    <div class="logoBox">
      <img src="@/assets/home/logo.png" height="45">
    </div>
    <div class="menuBox">
      <el-row class="tac">
        <el-col :span="24">
          <!-- <h5>默认颜色</h5> -->
          <el-menu
            default-active="2"
            class="el-menu-vertical-demo"
            @open="handleOpen"
            @close="handleClose"
          >
            <el-menu-item index="1">
              <i class="el-icon-menu"></i>
              <span slot="title">
                <router-link to="/index">平台首页</router-link>
              </span>
            </el-menu-item>
            <el-submenu index="2">
              <template slot="title">
                <i class="el-icon-location"></i>
                <span>知航览市</span>
              </template>
              <el-submenu index="2-1">
                <template slot="title">中转分析</template>
                <el-menu-item index="2-1-1">
                  <router-link to="/compare">机场中转对比</router-link>
                </el-menu-item>
                <el-menu-item index="2-1-2">
                  <router-link to="/analysis">中转机会分析</router-link>
                </el-menu-item>
              </el-submenu>
            </el-submenu>
            <el-submenu index="3">
              <template slot="title">
                <i class="el-icon-setting"></i>
                <span>用户管理</span>
              </template>
              <el-menu-item-group>
                <!-- <template slot="title">分组一</template> -->
                <el-menu-item index="3-1">个人中心</el-menu-item>
                <el-menu-item index="3-2">操作日志</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
export default {
  name: "LefeMenu",
  data() {
    return {};
  },
  methods: {},
  components: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a {
    color: #303113;
    text-decoration: none;
}
.router-link-active {
  color: rgb(64, 158, 255);;
}
.logoBox {
  height: 50px;
  padding-top: 10px;
  vertical-align: middle;
  border-bottom: 1px solid #ddd;
  /* background:seagreen; */
}
.menuBox {
  width: 100%;
}
.el-menu {
  border-right: none;
}
</style>



